/* * {
 margin: 0;
 padding: 0;
 box-sizing: border-box;
} */

.cart-navbar {
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  position: sticky;
  display: flex;
  background-color: white;
  align-items: center;
  justify-content: center;
  z-index: 1;
  width: 100%;
}

.cart-navbar .Shopping-flex,
.assets-nav {
  margin: 0px 5px;
}

.assets-nav .Shopping-card {
  padding: 0px 25px 10px 25px;
  text-align: center;
  cursor: pointer;
}

.Shopping-flex .Shopping-Box {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: white;
  margin: auto;
  justify-content: space-between;
  display: flex;
  position: absolute;
  margin-left: -125px;
  width: 34%;
  height: 450px;
  padding: 0px 5px;
}

.Shopping-flex .single-assets {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background-color: white;
  margin: auto;
  justify-content: space-between;
  position: absolute;
  width: 16%;
  padding: 0px 5px;
  margin-top: 8px;
}

.single-assets .fa-solid {
  position: absolute;
  left: 41.5%;
  font-size: 35px;
  margin-top: -20px;
  color: #efecec;
}

.single-assets .vehicle-card {
  padding: 5px 0px;
}
.vehicle-card a {
  display: block;
  text-decoration: none;
  color: black;
  width: 180px;
  padding: 7px 20px 7px 5px;
}

.vehicle-card a:hover {
  background-color: #efefef;
  color: #b132aa;
}

.Shopping-flex .assetsItems {
  text-decoration: none;
  color: black;
}

.Shopping-flex .assetsItems:hover {
  color: #b132aa;
}

.Shopping-card img {
  margin: auto;
  display: block;
  max-width: 70px;
  margin-bottom: 0px;
}

.Shopping-title a {
  text-decoration: none;
  display: block;
  color: black;
  width: 230px;
  padding: 7px 35px 7px 5px;
}

.Shopping-flex .assets-nav .Shopping-flex .Shopping-Box {
  justify-content: center;
  align-items: center;
  display: flex;
}

.assets-nav .Shopping-icon {
  margin: auto;
  display: block;
}

.Shopping-card .Shopping-icon {
  font-size: 15px;
  font-weight: 500;
}

.Shopping-flex .assets-nav:hover {
  color: #b132aa;
}

.fa-regular {
  font-size: 8px;
  font-weight: bold;
  margin-left: 5px;
  color: blue;
}

.fa-less-than:before,
.fa-greater-than:before {
  font-weight: 900;
  content: "\3c";
}

.assets-nav .Shopping-card .fa-rotate-270 {
  transition: all 0.4s ease-in-out;
  color: blue;
}

.assets-nav .Shopping-card:hover .fa-rotate-270 {
  transform: rotate(90deg);
  transition: all 0.4s ease-in-out;
}

.Shopping-Category .Category-section,
.Shopping-title a {
  display: flex;
  align-items: center;
  margin: 8px 0px;
  cursor: pointer;
}

.Shopping-Category .Category-section:hover,
.Shopping-title a:hover {
  background-color: #efefef;
  color: #b132aa;
}

.Category-section div {
  width: 180px;
  padding: 7px 20px 7px 5px;
}

.Shopping-assets .WomensCat-section {
  position: absolute;
  top: 44px;
}

.Shopping-assets .KidsCat-section {
  position: absolute;
  top: 86px;
}

.Shopping-assets .Shopping-Category {
  margin-left: 8px;
  margin-top: -52px;
  margin-left: 199px;
}

.Shopping-Box .Shopping-Category {
  width: 100%;
  padding: 0 5px;
}

.Shopping-Category .Mens-Fashion-items {
  margin-top: 0px;
  margin-left: 8px;
}

.Shopping-Category .Shopping-assets {
  display: flex;
  align-items: self-start;
}

.Shopping-Box .Shopping-Items {
  box-shadow: -5px 0 5px -5px #333;
  padding-left: 10px;
  max-width: 100%;
  background-color: white;
}

.Shopping-Box .fa-solid {
  position: absolute;
  left: 41.5%;
  font-size: 35px;
  margin-top: -20px;
  color: #efecec;
}

.MensCat-section:hover .Mens-title {
  display: block;
  transition: all 0.4s ease-in-out;
}

@media (max-width: 1020px) {
  .search-assets,
  .collapse,
  .collapse .logout-flex,
  .navbar .addtoCart {
    margin: auto;
  }

  .searchbar {
    background-color: transparent;
  }

  .searchbar .fa-search {
    font-size: 15px;
    color: #fffcfc;
    margin: auto;
  }

  .searchbar input {
    width: 0px;
  }

  .toggle-cart {
    width: 15px;
    height: 100px;
    background-color: blueviolet;
    position: absolute;
    top: 280px;
    left: -11px;
    z-index: 99;
    border-radius: 10px;
  }

  .cart-navbar {
    flex-direction: column;
    height: 770px;
    margin-top: 7px;
    border-top: 2px solid #efe6e6;
    transform: translateY(0%);
    transition: all 0.4s ease-in-out;
    height: 700px;
    z-index: 0;
  }

  .cart-close {
    transition: all 0.4s ease-in-out;
    transform: translateY(-100%);
  }

  .Shopping-Box .fa-solid {
    left: 35.5%;
  }

  .Shopping-Box .Shopping-Items {
    box-shadow: -5px 0 5px -5px #333;
    padding-left: 10px;
  }

  .Shopping-Box .Shopping-Category {
    padding: 0px;
  }

  .Shopping-flex .Shopping-Box {
    margin-left: -90px;
    height: auto;
    z-index: 1;
  }

  .Shopping-flex .single-assets,
  .Shopping-flex .Shopping-Box {
    width: unset;
  }

  .Category-section div {
    width: 160px;
    padding: 7px 0px 7px 5px;
  }
}

@media (max-width: 670px) {
  .home-CloneSlider .servicees-flex {
    flex-direction: column;
  }
}
