* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.shopping-navbar {
  top: 0;
  position: sticky;
  z-index: 99;
}

.navbar {
  top: 0;
  position: sticky;
  display: flex;
  padding: 2px 10px;
  /* background-color: #032158; */
  background-color: #b132aa;
  justify-content: space-between;
  z-index: 99;
}

.navbar .nav-logo {
  display: inline-grid;
  align-content: center;
  align-items: center;
  justify-content: center;
}

.navbar,
.navbar-nav,
.collapse {
  display: flex;
  text-decoration: none;
  flex-wrap: wrap;
  align-content: center;
  justify-content: center;
}

.collapse {
  margin: auto 0 auto 20px;
}

.navbar-nav .closebtn {
  position: absolute;
  top: 0;
  right: 30px;
  font-size: 35px;
  margin-left: 50px;
  text-decoration: none;
  color: #a07ecd;
  display: none;
}

.navbar-toggler {
  display: none;
}

.navbar-brand {
  cursor: pointer;
  display: flex;
  margin: auto;
}

.nav-link,
.nav-item,
.dropdown-item {
  text-decoration: none;
  list-style: none;
  padding: 5px;
  color: #fff;
}

.navbar .nav-logo .navbar-brand,
.navbar .navbar-nav .nav-item,
.navbar .navbar-nav .nav-item .dropdown-menu .dropdown-item {
  cursor: pointer;
}

.mr-auto {
  margin: auto 40px auto auto;
}

.nav-item:hover,
.nav-link:hover,
.dropdown-item:hover {
  color: purple;
  background-color: aquamarine;
  border-radius: 4px;
  cursor: pointer;
}

/*each dropdown css */
.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-menu {
  display: none;
  position: absolute;
  width: 175px;
  left: -31px;
  z-index: 1;
  background-color: #f9f9f9;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  padding: 5px;
}

.dropdown-menu .dropdown-item {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu .dropdown-item:hover {
  background-color: #f1f1f1;
}

.navbar-collapse .d-flex .btn-primary {
  text-decoration: none;
  color: white;
  background-color: #f07f22;
  border-color: 2px solid #f07f22;
  padding: 8px 12px;
  margin: 5px;
  outline: none;
  border-radius: 5px;
}

.navbar-collapse .logout-flex .btn-primary {
  text-decoration: none;
  color: white;
  background-color: #f07f22;
  border-color: 2px solid #f07f22;
  padding: 8px 12px;
  margin: 5px;
  outline: none;
  border-radius: 5px;
}

.d-flex .btn-primary:hover,
.navbar-collapse .logout-flex .btn-primary:hover {
  background-color: #f1592a;
  color: white;
}

.form-inline,
.d-flex {
  margin: auto;
}

.form-inline .btn {
  outline: none;
  padding: 12px;
  border-radius: 15px;
  cursor: pointer;
  background-color: yellowgreen;
}
.form-inline .btn:hover {
  background-color: green;
  color: white;
}

.form-control {
  margin: 10px;
  outline: none;
  padding: 12px 30px;
  border-radius: 20px;
}

/* Social Media Section */

.nav-socialmedia {
  margin: auto;
}

.media-flex a {
  cursor: pointer;
}

.nav-socialmedia .media-flex a img {
  height: 28px;
  width: 28px;
  margin: 5px;
  cursor: pointer;
  border-radius: 50%;
}

.navbar .addtoCart {
  margin: auto auto auto 30px;
  margin-right: 25px;
}

