/* Home Section */
/* Carousel Section for Home */
.Caroul-flex {
  justify-content: center;
  display: flex;
}

/* Post Section inside of Home */
.post-animate {
  position: absolute;
  animation: postmove 35s infinite;
  transition: linear;
  overflow: hidden;
  display: inline-block;
  z-index: 99;
  top: 0%;
}

.post-animate p {
  color: yellowgreen;
  font-weight: bold;
  text-decoration: none;
}

/* if user move on nes post than animatiion will be stoped and if remove mouse pointer than auto start */
.post-animate:hover {
  animation-play-state: paused;
}

@keyframes postmove {
  from {
    left: 95%;
  }
  to {
    left: -30%;
  }
}

/* About section */
.about-d-flex {
  margin: 59px 30px;
