  }

  .collapseNav {
    transform: translateX(0%);
  }

  .services-toggle {
    transform: translateY(0%);
  }

  .nav-socialmedia {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: cadetblue;
    width: 120px;
    height: 270px;
    display: flex;
    padding: 10px;
    transform: translateX(-100%);
    transition: all 0.4s ease-in-out;
  }

  .nav-socialmedia .media-flex a img {
    margin: 0;
  }

  .socialmedia-close {
    transform: translateX(0%);
  }

  .media-flex {
    flex-direction: column;
    display: flex;
  }

  .media-flex a {
    margin: 5px 0;
  }

  .OpenArrow {
    top: 118%;
  }

  .CloseArrow {
    top: 31%;
  }

  .OpenArrow,
  .CloseArrow {
    display: block;
  }

  .OpenArrow-btn {
    transform: translateX(100%);
  }

  .CloseArrow-btn {
    transform: translateX(0%);
  }

  .Opendw-toggle {
    transition: all 0.9s ease-in-out;
    display: block;
    text-decoration: none;
    margin-top: 5px;
    border-radius: 5px;
    transform: translateY(0%);
    flex-direction: column;
    background-color: #753a91;
    left: -57px;
    width: 230px;
    padding-top: 15%;
    padding-bottom: 8%;
  }

  .Closedw-toggle {
    transform: translateY(-270%);
    text-decoration: none;
    transition: all 0.9s ease-in-out;
    margin-top: 5px;
    left: -57px;
    width: 230px;
    border-radius: 5px;
    text-decoration: none;
    list-style: none;
    padding: 5px;
    color: #fff;
    background-color: #753a91;
  }

  .dropdown-menu .dropdown-item {
    text-decoration: none;
    list-style: none;
    padding: 5px;
    color: #fff;
    margin: 4% 0px;
    font-size: 20px;
  }

  .nav-item .dropdown,
  .dropdown .Closedw-toggle {
    background-color: transparent;
  }

  .home-container {
    overflow: hidden;
  }

  .home-container .footer {
    margin: auto;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 25px;
  }

  .footer .footer-social {
    padding: 0;
    align-items: center;
    justify-content: center;
    margin: auto;
  }

  .footer .footer-social,
  .footer-social .site,
  .footer .footer-menu {
    font-size: 12px;
    border: none;
    text-align: center;
  }
  .footer-social .site {
    border-bottom: 1px dashed #08e708;
    padding-bottom: 10px;
    margin-bottom: 5px;
  }

  .footer-menu .developed {
    right: 0;
    left: 2px;
  }
}
